import React, { useState } from "react";

import Guest from "./Guest";

import { Button, Flex, Input } from "@chakra-ui/react";
import axios from "axios";

function GuestParking() {
  const queryParameters = new URLSearchParams(window.location.search);
  const [guestData, setGuestData] = useState<Guest>();

  const [guestName, setGuestName] = useState<string>("");
  const [guestPhone, setGuestPhone] = useState<string>("");
  const [guestStay, setGuestStay] = useState<number>(0);
  const [parkingUrl, setParkingUrl] = useState<string>("");

  // example url: http://localhost:3000/?spot=6587f451

  var url = "http://18.207.124.9/api/parking/notify";

  const handleGuestName = (name: string) => {
    setGuestName(name);
    if (guestName.length > 100) {
      alert("Încearcă să găsești un nume mai scurt.");
      setGuestName("");
      name = "";
    }
  };

  const handleGuestPhone = (phone: string) => {
    setGuestPhone(phone);
    if (guestPhone.length > 12) {
      alert("Numărul de telefon nu pare valabil.");
      setGuestPhone("");
      phone = "";
    }
  };

  const handleGuestStay = (stayTime: number) => {
    setGuestStay(stayTime);
    if (guestStay < 0 || guestStay > 24) {
      alert("Încearcă să stai mai puțin de 24 de ore.");
      setGuestStay(0);
      stayTime = 0;
    }
  };

  const handleSubmit = () => {
    console.log(guestName);
    console.log(guestPhone);
    console.log(guestStay);

    var parkingUrl = queryParameters.get("spot") ?? "";

    const payload = {
      url: parkingUrl,
      guest: guestName,
      phoneNumber: guestPhone,
      estimatedStayHours: guestStay,
    };

    //setGuestData(payload);

    axios.post(url, payload).then((httpResponse) => {
      console.log(httpResponse.status);
      if (httpResponse.status.toString().startsWith("20")) {
        alert("Mesajul tău a fost trimis!");
      }
    });
  };

  return (
    <>
      <Flex marginTop="10px" flexDir="column">
        <Input
          placeholder="Nume"
          onChange={(event) => handleGuestName(event?.target?.value)}
        />
        <Flex marginTop="5px">
          <Input
            marginRight="5px"
            type="number"
            placeholder="Nr. de telefon:"
            onChange={(event) => handleGuestPhone(event?.target?.value)}
          />
          <Input
            type="number"
            placeholder="Câte ore vei sta?"
            onChange={(event) => handleGuestStay(event?.target?.valueAsNumber)}
          />
        </Flex>

        <Button
          marginTop="10px"
          backgroundColor="#2672ed"
          onClick={handleSubmit}
        >
          Anunță
        </Button>
      </Flex>
    </>
  );
}

export default GuestParking;
